.h2-main{
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    text-align: left;
    font-size: 18px;
    color:#E6E5EA ;
}

.green{
    color: #A4FFAF;
    font-size: 32px;
}

.container1{
    margin-left: 32px;
    margin-right: 32px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
@media (width <=430px){
    .h2-main{
        font-size: 16px;
    }
    .green{
        font-size: 24px;
    }
    .container1{
        margin-left: 16px;
        margin-right: 16px;
    }
}

@media (430 <= width <=768px){
    .h2-main{
        font-size: 18px;
    }
    .green{
        font-size: 32px;
    }
    .container1{
        margin-left: 32px;
        margin-right: 32px;
    }
}