.RadioBtn{

    padding-left: 32px;
}

.LabelText{
    font-family: 'JetBrains Mono', monospace;
    font-size: 18px;
    font-weight: bold;

}
.checkboxWrapper{
    display: flex;
    flex-direction: row;
    justify-content: left;
    justify-items: center;
    align-items: center;
    margin: 0;
    padding: 0;
    
}

.h2-check{
    text-align: center;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 18px;
    color: #E6E5EA;
    margin: 0;
}
@media (width <=430px){
    .h2-check{
        font-size: 16px;
    }
}

@media (430 <= width <=768px){
    .h2-check{
        font-size: 18px;
    }
}