body {
    background-color: #18171F;
    height: 100vh;
    margin: 0;
}

#hero{
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    width: 600px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 133px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.h1-main{
    text-align: center;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 24px;
    color: #817D92;
    margin: 0;
}

.Pass-area{

    background-color: #24232C;
    width: 540px;
    height: 80px;
    margin-top: 33px;
}

.Check-area{

    background-color: #24232C;
    width: 540px;
    height: 528px;
    margin-top: 24px;
}

footer {
    position: absolute;
    text-align: center;
    width: 100%;
    height: 2.5rem;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 24px;
    color: #E6E5EA;
    margin-top: 50px;
  }

  @media (width <=430px) {
    .container{
        width: 343px;
    }
    .Pass-area{
        width: 343px;
        height: 64px;
        margin-top: 16px;
    }

    .Check-area{
        width: 343px;
        height: 423px;
    }

    .h1-main{
        font-size: 16px;
    }

    footer{
        font-size: 18px;
    }
  }

  @media (430 <= width <=768px){
    .container{
        width: 600px;
    }
    .Pass-area{
        width: 540px;
        height: 80px;
        margin-top: 31px;
    }

    .Check-area{
        width: 540px;
        height: 528px;
    }

    .h1-main{
        font-size: 16px;
    }

    footer{
        font-size: 18px;
    }
  }