.password{
    text-align: left;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 32px;
    color: #E6E5EA;
    width: 192px;
}

.PasswordTextWrapper{
    margin-left:32px;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-items: center;
    justify-content: space-between;

}

.CopyBtn-wrapper{
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    align-items: center;
    height: 80px;

}

.CopyBtn{
    background-color: #24232C;
    border: 0;
    margin-left: 16px;
    margin-right: 28px;
    } 
.copySvg:hover{

    filter: brightness(0) saturate(100%) invert(100%) sepia(20%) saturate(3123%) hue-rotate(195deg) brightness(106%) contrast(83%);
}    
.spanCopid{
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 18px;
    color: #A4FFAF;
}

@media (width <=430px){
    .password{
        font-size: 18px;
        width: 144px;
       

    }
    .spanCopid{
        font-size: 16px;
    }
    .PasswordTextWrapper{
        margin-left: 16px;
        margin-top: 7px;
        height: 64px;
    }

    .CopyBtn{
        margin-right: 15px;
        margin-bottom: 24px;
    }
}

@media (430 <= width <=768px){
    .password{
        font-size: 24px;
        width: 192px;
       

    }
    .spanCopid{
        font-size: 18px;
    }
    .PasswordTextWrapper{
        margin-left: 32px;
        margin-top: 31px;
        height: 80px;
    }

    .CopyBtn{
        margin-right: 28px;
    }
}