.Strenght-Box{
    width: 412px;
    height: 72px;
    background-color: #18171F;
    margin-top: 32px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    justify-items: center;
    align-items: center;
    padding-left: 32px;
    padding-right: 32px;
}

.h1-main{
    text-align: center;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 18px;
    color: #817D92;
}

.h2-main-text{
    text-align: center;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 24px;
    color: #E6E5EA;
}

.Wrapper{

    width: 209px;
    display: flex;
    flex-direction: row;
    gap: 15.5px;
    justify-content:space-between;
    justify-items: center;
    align-items: center;
}

.Wrapper-line{
    width: 72px;
    display: flex;
    flex-direction: row;
    gap: 7.5px;
}
.line{
    width: 10px;
    height: 28px;
    border: solid;
    border-color: #E6E5EA;
    border-width: 2px;
}

.lineRed{
    background-color: #F64A4A;
    width: 10px;
    height: 28px;
    border: solid;
    border-color: #E6E5EA;
    border-width: 2px;
}

.lineOrange{
    background-color: #FB7C58;
    width: 10px;
    height: 28px;
    border: solid;
    border-color: #E6E5EA;
    border-width: 2px;
}
.lineYellow{
    background-color: #F8CD65;
    width: 10px;
    height: 28px;
    border: solid;
    border-color: #E6E5EA;
    border-width: 2px;
}
.lineGreen{
    background-color: #A4FFAF;
    width: 10px;
    height: 28px;
    border: solid;
    border-color: #E6E5EA;
    border-width: 2px;
}

@media (width <=430px){
    .Strenght-Box{
        width: 311px;
        height: 56px;
        margin-top: 14px;
        padding: 0;
    }
    .Wrapper{
        width: 145px;
        margin-right: 16px;
    }
    .Wrapper-line{
        width: 145px;
    }
    .h1-main{
        font-size: 16px;
        margin-left: 16px;
    }
    .h2-main-text{
        font-size: 18px;
    }
}

@media (430 <= width <=768px){
    .Strenght-Box{
        width: 476px;
        height: 72px;
        margin-top: 32px;
    }
    .Wrapper-line{
        width: 166.5px;
    }
    .h1-main{
        font-size: 18px;
    }
    .h2-main-text{
        font-size: 24px;
    }
}
