.BtnMain{
    background-color: #A4FFAF;
    width: 476px;
    height: 65px;
    text-align: center;
    font-family: 'JetBrains Mono', monospace;
    font-weight: bold;
    font-size: 18px;
    color: #24232C;
}
.BtnMain:hover{
    background-color: #24232C;
    border-color: #A4FFAF;
    border-width: 3px;
    color: #A4FFAF;
}
.BtnWrapper{
    width: 476px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 32px;
}

@media (width <=430px){
    .BtnMain{
        width: 311px;
        height: 56px;
    }
    .BtnWrapper{
        width: 311px;
        margin-top: 16px;
    }
}
@media (430 <= width <=768px){
    .BtnMain{
        width: 476px;
        height: 65px;
    }
    .BtnWrapper{
        width: 476px;
        margin-top: 32px;
    }
}